
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: "Source Sans Pro", sans-serif;
  background-color: #f8f8f8;
}

a{
  text-decoration: none;
}

ul{
  list-style-type: none;
}

.container{
  max-width: 85%;
  margin: auto;
  @media screen and (max-width: 920px) {
    max-width: 95%;
  }
}

.headerContainer{
  max-width: 90%;
  margin: auto;
}

.flexSB{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

button{
  background-color: none;
  border: none;
  outline: none;
  font-weight: bold;
}

.outline-btn{
  border: 2px solid #000;
  padding: 14px 40px; 
  transition: 0.5s;
}

::-webkit-scrollbar {
  width: 10px; 
}

/* Track */
::-webkit-scrollbar-track {
  background: #f8f8f8; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}




/*_______________________HEADER_________________________*/


header{
  height: 10vh;
  line-height: 10vh;
  background-color: #fff;
  box-shadow: 0 7px 2px -3px rgba(89,89,89,0.02);
  nav{
    .flexSB{
      @media screen and (max-width: 968px) {
        position: absolute;
        top: 10vh;
        left: 0;
        height: 70vh;
        padding: 30px;
        width: 100%;
        flex-direction: column;
        background-color: #a06c47;
        transform: translateX(100%);
        transition: 0.5s;
        z-index: 999;
      }
      li{
        a{
          color: #767676;
          margin: 0 20px;
          font-size: 18px;
          font-weight: 500;
          @media screen and (max-width: 968px) {
            color: #fff;
          }
        }
      }
    }
  }
  .icon{
    font-size: 20px;
    display: none;
    @media screen and (max-width: 968px) {
      display: block;
    }
  }
  .flexSB.active{
    @media screen and (max-width: 968px) {
      transform: translateX(0);
    }
  }
}
.outline-btn{
  &:hover{
    background-color: #000;
    color: #fff;
    cursor: pointer;
  }
}

/*_______________________HEADER_________________________*/



/*_______________________HOME_________________________*/


.home{
  height: auto;
  position: relative;
  @media screen and (max-width: 968px) {
    height: auto;
  }
  .left{
    margin: 15% 0;
    h1{
      font-size: 100px;
      span{
        color: #fff;
        -webkit-text-stroke: 1px #767676;
      }
      @media screen and (max-width: 968px) {
        font-size: 90px;
      }
      @media screen and (max-width: 768px) {
        font-size: 70px;
      }
      @media screen and (max-width: 618px) {
        font-size: 50px;
      }
      @media screen and (max-width: 450px) {
        font-size: 35px;
      }
    }
    h3{
      font-size: 20px;
      color: #767676;
      font-weight: 600;
      @media screen and (max-width: 450px) {
        font-size: 13px;
      }
    }
  }
  img{
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    clip-path: polygon(28% 0, 100% 0, 100% 50%, 100% 100%, 26% 100%, 0 50%);
    object-fit: cover;
  }
}


/*_______________________HOME_________________________*/

/*_______________________ABOUT_________________________*/


.baget{
  padding: 10px 20px;
  background-color: rgba(0,0,0,0.05);
  font-weight: 600;
  text-transform: uppercase;
}
.primary-btn{
  padding: 20px 60px;
  background-color: #000;
  color: #fff;
  border: 2px solid #000;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: 0.5s;
  &:hover{
    background-color: transparent;
    color: #000;
    border: 2px solid #000;
  }
}
.about{
   background-color: #fff;
   padding: 100px 0;
   .container{
    @media screen and (max-width: 1200px) {
      max-width: 95%;
    }
   }
   .wrap{
     display: flex;
     @media screen and (max-width: 1030px) {
      flex-direction: column-reverse;
    }
    .row{
      @media screen and (max-width: 1030px) {
        width: 100%;
      }
    }
   }
   .left{
    img{
       width: 100%;
      @media screen and (max-width: 1480px) {
        margin: 0 50px;
      }
      @media screen and (max-width: 1320px) {
        margin: 0 10px;
      }
      @media screen and (max-width: 1220px) {
        margin: 0;
      }
    }
   }
   .right{
     padding: 70px 20px;
     @media screen and (max-width: 1320px) {
      padding: 70px 30px;
    }
    @media screen and (max-width: 1030px) {
      text-align: center;
      margin: auto;
      padding: 70px 0;
    }
   }
   h2{
     font-size: 40px;
     margin: 20px 0;
     @media screen and (max-width: 360px){
      font-size: 30px;
    }
   }
   p{
     margin-top: 20px;
     margin-bottom: 20px;
     font-size: 18px;
     line-height: 30px;
     color: #767676;
   }
}




/*_______________________ABOUT_________________________*/

/*_______________________PORTFOLIO_________________________*/


.grid{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  @media screen and (max-width: 1030px) {
    grid-template-columns:repeat(2,1fr);
  }
  @media screen and (max-width: 768px) {
    grid-template-columns:repeat(1,1fr);
  }
}

.Portfolio{
  position: relative;
  .left{
    padding: 20px 0;
  }
  .head{
    // width: 90%;
    @media screen and (max-width: 768px) {
      display: grid;
      grid-template-columns: repeat(1fr);
      text-align: center;
    }
  }
  .flexSB{
    @media screen and (max-width: 768px) {
      justify-content: center;
    }
  }
  .links{
    @media screen and (max-width: 768px) {
      padding: 15px 0;
    }
  }
  .title{
    font-size: 30px;
    margin-top: 20px;
  }
  li{
    margin-left: 30px;
    font-size: 17px;
    cursor: pointer;
    transition: 0.5s;
    text-transform: capitalize;
    &:hover{
      background-color: #000;
      color: #fff;
      padding: 5px 10px;
    }
    @media screen and (max-width: 392px) {
      margin-left: 15px;
    }
  }
  .active{
      background-color: #000;
      color: #fff;
      padding: 5px 10px;
  }
  img{
    width: 100%;
    height: 100%;
    cursor: pointer;
  }
  .box{
    position: relative;
    .overlay{
      position: absolute;
      bottom: 0;
      width: 100%;
      padding: 20px 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: rgba(255,255,255,0.6);
      z-index: 1;
      opacity: 0;
      transition: 0.5s;
      h1{
        margin: 0;
        font-size: 20px;
      }
    }
    &:hover{
      .overlay{
        opacity: 1;
      }
    }
  }
}


/*_______________________PORTFOLIO_________________________*/




/*_______________________SKILLS_________________________*/


.row{
  width: 50%;
  @media screen and (max-width: 768px) {
    width: 90%;
  }
  @media screen and (max-width: 480px) {
    width: 100%;
  }
}

.skill{
  padding: 5% 0;
  background-color: #fff;
  .flexSB{
    @media screen and (max-width: 768px) {
      flex-direction: column;
      text-align: center;
    }
  }
  .left{
    padding-right: 50px;
    @media screen and (max-width: 480px) {
      padding-right: 0;
    }
    @media screen and (max-width: 768px) {
      padding-right: 0;
    }
    h1{
      padding: 10px 0;
      font-size: 26px;
      font-weight: 600;
    }
    p{
      padding: 10px 0;
      line-height: 40px;
      font-size: 18px;
      color: #767676;
    }
  }
}

.progress{
  background-color: #d5d5d5;
  border-radius: 20px; 
  position: relative;
  margin: 80px 0;
  height: 5px;
  width: 90%;
  h4{
    position: absolute;
    color: #000;
    top: -30px;
    font-size: 18px;
    font-weight: 500;
    &:nth-child(2){
      right: 0;
      font-size: 17px;
      font-weight: 400;
    }
  }
  .progress-done{
    background-color: #000;
    border-radius: 20px;
    color: #fff;
    height: 100%;
    opacity: 0;
    transition: 0.5s ease;
  }
}


/*_______________________SKILLS_________________________*/




/*_______________________WRAPPER_________________________*/


.wrapper{
  background-image: url(./images/video.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 90vh;
  color: #fff;
  text-align: center;
  .container{
    max-width: 50%;
    margin: auto;
    @media screen and (max-width: 968px) {
      max-width: 70%;
    }
    @media screen and (max-width: 768px) {
      max-width: 90%;
    }
    @media screen and (max-width: 614px) {
      max-width: 98%;
    }
  }
  .icon{
    width: 100px;
    height: 100px;
    text-align: center;
    line-height: 100px;
    @media screen and (max-width: 550px) {
      width: 60px;
    }
  }
  h1{
    font-size: 50px;
    margin: 40px 0;
    @media screen and (max-width: 550px) {
      font-size: 40px;
    }
    @media screen and (max-width: 450px) {
      font-size: 30px;
    }
    @media screen and (max-width: 340px) {
      font-size: 27px;
    }
  }
  .outline-btn{
    font-size: 20px;
    font-weight: 500;
    padding: 20px 60px;
    border: 2px solid #fff;
    color: #000;
    @media screen and (max-width: 550px) {
      padding: 17px 50px;
      font-size: 18px;
    }
    @media screen and (max-width: 450px) {
      padding: 14px 40px;
      font-size: 15px;
    }
  }
}


/*_______________________WRAPPER_________________________*/



/*_______________________BRANDING_________________________*/


.branding{
  padding: 50px 0;
  background-color: #fff;
  .container{
    @media screen and (max-width: 1200px) {
      max-width: 95%;
    }
  }
  .grid2{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
    @media screen and (max-width: 1050px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media screen and (max-width: 768px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 520px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  .box{
    text-align: center;
    img{
      filter: brightness(2);
      transition: 0.5s;
      &:hover{
        filter: brightness(1);
        cursor: pointer;
      }
    }
  }
}


/*_______________________BRANDING_________________________*/






/*_______________________TESTIMONIALS_________________________*/


.testimonials{
  padding: 80px 0;
  .flexSB{
    @media screen and (max-width: 768px) {
      flex-direction: column;
      text-align: center;
    }
  }
  h1{
    font-size: 35px;
    margin-top: 30px;
    @media screen and (max-width: 768px) {
      margin-bottom: 30px;
    }
  }
  .left{
    padding-right: 100px;
    @media screen and (max-width: 768px) {
      padding-right: 0;
    }
  }
  .details{
    margin-top: 50px;
    img{
      width: 80px;
      height: 80px;
      border-radius: 50%;
      object-fit: cover;
    }
    .name{
      margin: 20px 0;
    }
  }
  p{
    color: #767676;
    line-height: 30px;
  }
}

/*_______________________TESTIMONIALS_________________________*/



/*_______________________BLOG_________________________*/


.blog{
  background-color: #fff;
  padding: 100px 0;
  h1{
    margin: 20px 0;
  }
  h2{
    margin-bottom: 20px;
  }
  .content{
    span{
      color: #767676;
      font-style: italic;
    }
  }
  img{
    width: 100%;
    object-fit: contain;
  }
}


/*_______________________BLOG_________________________*/




/*_______________________WORK_________________________*/


.work{
  padding: 100px 0;
  background-image: url(./images/wrapper.jpg);
  background-repeat: no-repeat;
  height: 80vh;
  text-align: center;
  position: relative;
  z-index: 9;
  &::after{
    content: "";
    position: absolute;
    top: -1px;
    left: 0;
    background-image: url(./images/paper.svg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 10vh;
    transform: rotate(-180deg);
  }
  &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 80vh;
    background-color: rgba(0,0,0,0.4);
  }
  .container{
    position: relative;
    z-index: 9;
  }
  h1{
    font-size: 80px;
    color: #fff;
    margin-top: 15%;
    @media screen and (max-width: 968px) {
      font-size: 70px;
    }
    @media screen and (max-width: 768px) {
      font-size: 50px;
    }
    @media screen and (max-width: 550px) {
      font-size: 40px;
    }
    @media screen and (max-width: 430px) {
      font-size: 26px;
      margin-top: 50%;
    }
  }
  button{
    background-color: #fff;
    color: #000;
    border: 2px solid #fff;
    margin-top: 40px;
    @media screen and (max-width: 768px) {
      padding: 17px 30px;
    }
    @media screen and (max-width: 430px) {
      padding: 10px 18px;
    }
    &:hover{
      color: #fff;
      border: 2px solid #fff;
    }
  }
}


/*_______________________WORK_________________________*/


/*_______________________FOOTER_________________________*/


footer{
  padding: 50px 0;
  text-align: center;
  background-color: #000;
  color: #fff;
  p{
    color: #fff;
  }
}

/*_______________________FOOTER_________________________*/